.btn-violet {
  background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
  box-shadow: 0px 0px 16.9px 5px rgb(52, 0, 119);
  &:hover {
    box-shadow: 0px 0px 16.9px 12px rgb(52, 0, 119);
  }
  span {
    line-height: 120%;
    font-weight: 500;
    color: #fff;
  }
}
.settings {
  position: fixed;
  z-index: 5;
  top: -105%;
  right: 15px;
  max-width: 400px;
  width: 100%;
  min-height: 600px;
  background: #040404;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #fff;
  padding: 20px 20px 0 20px;
  margin: 0 auto;
  transition: all 0.3s;
  overflow: hidden;
  &--active {
    top: 0;
  }
  &:before {
    content: '';
    position: absolute;
    top: -220px;
    right: -220px;
    width: 350px;
    height: 350px;
    background: linear-gradient(180.00deg, rgba(0, 194, 255, 0),rgb(255, 41, 195) 100%);
    filter: blur(120.08px);
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    left: -220px;
    bottom: -220px;
    width: 350px;
    height: 350px;
    background: linear-gradient(180.00deg, rgb(23, 59, 128),rgb(17, 87, 62) 100%);
    filter: blur(120.08px);
    pointer-events: none;
  }
  &__top {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  &__title {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    font-size: 20px;
    line-height: 120%;
    font-weight: 500;
    span {
      width: 18px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  .title-btn,
  .title-btn2 {
    cursor: pointer;
  }
  &__btn-wrap {
    width: 18px;
    flex-shrink: 0;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__btn2 {
    display: none;
  }
  &__btn--active {
    display: none;
  }
  &__btn2--active {
    display: block;
  }
  &__search {
    position: relative;
    z-index: 2;
    margin-top: 12px;
    .search-input {
      width: 100%;
      height: 40px;
      font-size: 14px;
      line-height: 120%;
      color: #fff;
      background: transparent;
      border: 1px solid #601DC5;
      border-radius: 8px;
      padding: 0 41px 0 20px;
      &::placeholder {
        color: #fff;
      }
    }
    .search-btn {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      width: 16px;
      background: transparent;
      padding: 0;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  &__overflow {
    width: calc(100% + 8px);
    height: 440px;
    padding-right: 8px;
    margin-top: 14px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
      border-radius: 10px;
      margin-bottom: 20px;
    }
    &::-webkit-scrollbar-thumb {
      background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
      border-radius: 10px;
      margin-bottom: 20px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    align-items: start;
    gap: 10px;
  }
  &__item {
    min-height: 48px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 5px;
    background: rgba(255, 255, 255, 0.06);
    backdrop-filter: blur(289.3px);
    border-radius: 6px;
    padding: 5px 25px 5px 15px;
    &:last-child {
      margin-bottom: 20px;
    }
  }
  &__item-group {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__date {
    font-size: 14px;
    line-height: 105%;
    color: #ACACAC;
  }
  &__usdt {
    flex-shrink: 0;
    font-size: 14px;
    line-height: 105%;
    span {
      color: #E88D23;
    }
  }
  &__text {
    position: relative;
    flex-grow: 1;
    p {
      font-size: 14px;
      line-height: 120%;
      color: #23D0E8;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:1;
      -webkit-box-orient: vertical;
    }
    span {
      color: #ACACAC;
    }
  }
  &__text2 {
    font-size: 14px;
    line-height: 105%;
  }
  &__info {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 15px;
    width: 15px;
    flex-shrink: 0;
    cursor: pointer;
    transition: all 0.3s;
    img {
      width: 100%;
    }
  }
  &__money {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 14px;
    line-height: 105%;
    span {
      width: 16px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
  &__check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: linear-gradient(90.00deg, rgb(24, 254, 156, 0.6),rgb(20, 135, 122, 0.6) 82.189%);
      &:after {
        left: calc(100% - 17.5px);
      }
    }
    label {
      position: relative;
      display: block;
      width: 36.5px;
      height: 19px;
      background: linear-gradient(90.00deg, rgb(254, 82, 24, 0.6),rgb(135, 20, 20, 0.6) 82.189%);
      border-radius: 24px;
      transition: all 0.5s;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 2px;
        transform: translate(0, -50%);
        width: 15.5px;
        height: 15.5px;
        background: #fff;
        border-radius: 50%;
        transition: all 0.5s;
      }
    }
  }
  &__edit {
    width: 20px;
    flex-shrink: 0;
    cursor: pointer;
    transition: all 0.3s;
    img {
      width: 100%;
    }
  }
}
.transactions {
  position: relative;
  z-index: 2;
  margin-top: 24px;
  &--active {
    display: none;
  }
}
.settings__form {
  position: relative;
  z-index: 2;
  width: calc(100% + 8px);
  height: 478px;
  display: none;
  padding-right: 8px;
  margin-top: 24px;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
    border-radius: 10px;
    margin-bottom: 20px;
  }
  &::-webkit-scrollbar-thumb {
    background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
    border-radius: 10px;
    margin-bottom: 20px;
  }
  &--active {
    display: block;
  }
  &-wrap {
    display: grid;
    grid-template-columns: 100%;
    gap: 11px;
    margin-top: 14px;
    margin-bottom: 20px;
  }
  &-group {
    .input-title {
      display: block;
      font-size: 14px;
      line-height: 120%;
      color: #ACACAC;
      margin-bottom: 8px;
    }
    input {
      width: 100%;
      height: 40px;
      font-size: 14px;
      line-height: 120%;
      color: #fff;
      background: transparent;
      border: 1px solid #601DC5;
      border-radius: 8px;
      padding: 0 20px;
      &::placeholder {
        color: #fff;
      }
    }
    &:hover {
      &~.settings__form-group {
        pointer-events: none;
      }
    }
  }
  &-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
  }
  .select__wrap {
    position: relative;
    &--active {
      .select__list {
        transform: translateY(0);
        opacity: 1;
        pointer-events: auto;
      }
      .select-arrow {
        svg {
          transform: rotate(-180deg);
          path {
            stroke: #601DC5;
          }
        }
      }
    }
  }
  .select__list-default {
    position: relative;
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    line-height: 120%;
    color: #fff;
    background: transparent;
    border: 1px solid #601DC5;
    border-radius: 8px;
    padding: 5px 20px;
    cursor: pointer;
    &:after {
      content: '';
      position: absolute;
      bottom: -5px;
      left: 0;
      width: 100%;
      height: 5px;
      background: transparent;
      opacity: 0;
    }
  }
  .select__list-default2 {
    padding-left: 50px;
  }
  .select__current {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 14px;
    line-height: 120%;
    span {
      max-width: 18px;
      img {
        max-width: 100%;
      }
    }
  }
  .select__icon {
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translate(0, -50%);
  }
  .select-arrow {
    width: 11px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: auto;
      transition: all 0.3s;
    }
  }
  .select__list {
    position: absolute;
    z-index: 2;
    top: calc(100% + 5px);
    left: 0;
    transform: translateY(-5px);
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    background: #040404;
    border: 1px solid #601DC5;
    border-radius: 8px;
    padding: 15px 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    li {
      display: flex;
      align-items: center;
      gap: 10px;
      font-size: 14px;
      line-height: 120%;
      cursor: pointer;
      span {
        max-width: 18px;
        img {
          max-width: 100%;
        }
      }
    }
  }
  .select__list2 {
    width: calc(100% + 8px);
    height: 120px;
    padding-right: 8px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
      border-radius: 10px;
      margin: 5px 0;
    }
    &::-webkit-scrollbar-thumb {
      background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
      border-radius: 10px;
      margin: 5px 0;
    }
  }
  .select__list3 {
    width: calc(100% + 8px);
    height: 90px;
    padding-right: 8px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #fff;
      border-radius: 10px;
      margin: 5px 0;
    }
    &::-webkit-scrollbar-thumb {
      background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
      border-radius: 10px;
      margin: 5px 0;
    }
  }
  &-subtitle {
    font-size: 14px;
    line-height: 120%;
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      background: linear-gradient(90.00deg, rgb(194, 98, 237),rgb(89, 15, 184) 100%);
      &:after {
        left: calc(100% - 19px);
      }
    }
    label {
      position: relative;
      display: block;
      width: 40px;
      height: 20px;
      background: #ACACAC;
      border-radius: 27px;
      transition: all 0.5s;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 2px;
        transform: translate(0, -50%);
        width: 17px;
        height: 17px;
        background: #fff;
        border-radius: 50%;
        transition: all 0.5s;
      }
    }
  }
  &-btn {
    height: 40px;
    border-radius: 24px;
    padding: 6px 60px;
    margin: 0 auto;
    span {
      font-size: 14px;
    }
  }
}
.configs {
  position: relative;
  z-index: 2;
  display: none;
  margin-top: 24px;
  &--active {
    display: block;
  }
}



.alert {
  position: fixed;
  top: 15px;
  right: 15px;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #040404;
  backdrop-filter: blur(289.3px);
  border-radius: 13px;
  font-size: 32px;
  line-height: 105%;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  overflow: hidden;
  &:before {
    content: '';
    position: absolute;
    top: -160px;
    right: -100px;
    width: 180px;
    height: 180px;
    background: linear-gradient(180.00deg, rgba(0, 194, 255, 0),rgb(255, 41, 195) 100%);
    filter: blur(50px);
    pointer-events: none;
  }
  &:after {
    content: '';
    position: absolute;
    left: -50px;
    bottom: -30px;
    width: 100px;
    height: 100px;
    background: linear-gradient(180.00deg, rgb(23, 59, 128),rgb(17, 87, 62) 100%);
    filter: blur(24px);
    pointer-events: none;
  }
  span {
    position: relative;
    z-index: 2;
  }
}



@media screen and (max-width: 450px) {
  .settings {
    left: 50%;
    right: unset;
    transform: translate(-50%, 0);
    max-width: calc(100% - 20px);
    &__item {
      min-height: 40px;
      padding: 5px 20px 0 10px;
    }
    &__item-group {
      gap: 5px;
    }
    &__date {
      font-size: 12px;
    }
    &__usdt {
      font-size: 12px;
    }
    &__text {
      &:after {
        width: 24px;
        height: 24px;
      }
      p {
        font-size: 12px;
      }
    }
    &__text2 {
      font-size: 12px;
    }
    &__money {
      font-size: 12px;
    }
    &__edit {
      width: 16px;
    }
  }
}